'use client'

import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { validateEmail, validatePassword, validateUsername } from '@strife/common';
import { useRouter } from 'next/navigation';
import { ChangeEvent, Fragment, useState } from 'react';
import WindowPage from '../../components/pages/WindowPage';
import di from '../../src/di';
import '../../src/services/user.service';
import '../../src/services/account.service';
import '../../src/services/authentication.service';
import { AuthenticationServiceServiceId } from '../../src/services/authentication.service.interface';

export default function LoginPage() {
  const router = useRouter();
  const [ email, setEmail ] = useState('');
  const [ emailError, setEmailError ] = useState<string | undefined>();
  const [ password, setPassword ] = useState('');
  const [ passwordError, setPasswordError ] = useState<string | undefined>();
  const [ error, setError ] = useState<string | undefined>();

  return (
    <WindowPage>
      <form className="flex flex-col gap-[32px] p-8" onSubmit={onSubmitLogin}>
        <Typography variant="h2" align="center" fontWeight={700}>strife</Typography>
        <div className="flex flex-col gap-[8px]">
          {
            error == undefined
              ? <Fragment />
              : <Alert variant="filled" severity="error">{error}</Alert>
          }
          <TextField name="email" type="email" value={email} onChange={onChangeEmail} error={emailError != undefined} helperText={emailError} placeholder="email" />
          <TextField name="password" type="password" value={password} onChange={onChangePassword} error={passwordError != undefined} helperText={passwordError} placeholder="password" />
          <Button type="submit">Login</Button>
          <Link href="/login" className="text-center" fontSize={'small'}>Don't have an account yet?</Link>
        </div>
      </form>
    </WindowPage>
  );

  function onChangeEmail(event: ChangeEvent<HTMLInputElement>): void {
    const email = event.target.value;
    setEmail(email)
    const error = email.length == 0 
      ? undefined
      : validateEmail(email);
    setEmailError(error ? error.message : undefined);
  }

  function onChangePassword(event: ChangeEvent<HTMLInputElement>): void {
    const password = event.target.value;
    setPassword(password);
    const error = password.length == 0
      ? undefined
      : validatePassword(password);
    setPasswordError(error ? error.message : undefined);
  }

  async function onSubmitLogin(event: React.FormEvent<HTMLFormElement>): Promise<void> {
    event.preventDefault();

    if (emailError || passwordError)
      return;

    const authenticationService = await di.getAsync(AuthenticationServiceServiceId);

    try {
      const authenticationData = await authenticationService.login({ email, password });
      onLoginSuccess();
    } catch (error: any) {
      console.log(error);
      setError(error);
    }
  }

  async function onLoginSuccess(): Promise<void> {
      setError(undefined);
      setEmail('');
      setEmailError(undefined);
      setPassword('');
      setPasswordError(undefined);

      router.push('/home');
  }

}
