import { type AuthenticationData } from '@strife/common';
import { injectable } from 'inversify';
import di from '../di';
import { UserServiceId, type IUserService } from '../user.interface';

@injectable()
export default class UserService implements IUserService {

  private _authentication?: AuthenticationData;
  public get authentication(): AuthenticationData | undefined {
    return this._authentication;
  }

  public login(authenticationData: AuthenticationData): void {
    this._authentication = authenticationData;
  }

  public logout(): void {
    this._authentication = undefined;
  }

}

di.bind(UserServiceId).to(UserService).inSingletonScope();