'use client'

import Paper from '@mui/material/Paper';
import { JSX } from 'react';
import Container from './Container';

export default function WindowPage({ children }: Readonly<{ children: React.ReactNode }>): JSX.Element {
  return (
    <Container>
      <div className="font-sans grid grid-rows-[20px_1fr_20px] items-center justify-items-center min-h-screen p-8 pb-20 gap-16 sm:p-20">
        <main className="row-start-2 items-center sm:items-start w-full md:w-lg">
          <Paper elevation={4}>
            {children}
          </Paper>
        </main>
      </div>
    </Container>
  );
}
